.gpt3__features {
    display: flex;
    justify-content: space-between;
}

.gpt3__features__headings {
    margin-right: 5rem;
}

.gpt3__features__headings h1 {
    font-weight: 800;
    font-size: 34px;
    font-family: var(--font-family);
    line-height: 45px;
    width: 426px;
}

.gpt3__features__headings p {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 30px;
    color: #FF8A71;
    margin-top: 2rem;
}

.gpt3__features-container {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
}


@media screen and (max-width: 990px) {
    .gpt3__features {
        flex-direction: column;
    }

    .gpt3__features__headings{
        margin: 0 0 2rem 0;
    }
    .gpt3__features__headings h1 {
        width: 380px;
    }
}


@media screen and (max-width: 550px) {
    .gpt3__features__headings h1 {
        font-size: 28px;
        line-height: 38px;
        max-width: 300px;
    }
}