    .gpt3__navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;

    padding: 2rem 6rem;
}

.gpt3__navbar-links {
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.gpt3__navbar-links_logo {
    margin-right: 2rem;
}

.gpt3__navbar-links_logo img {
    width: 62.56px;
    height: 16.02px;
}

.gpt3__navbar-links_container {
    display: flex;
    flex-direction: row;
}

.gpt3__navbar-sign {
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.gpt3__navbar-links_container a,
.gpt3__navbar-sign a,
.gpt3__navbar-menu_container a {
    color: #fff;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    text-transform: capitalize;
    margin: 0 1rem;
    cursor: pointer;
}

.gpt3__navbar-links_container a {
    position: relative;
    transition: 0.3s;
}

.gpt3__navbar-links_container a:hover {
    color: #d9859f;
}

.gpt3__navbar-links_container a::before {
    position: absolute;
    content: '';
    bottom: -3px;
    left: 0;
    transition: 0.3s;
    width: 0;
    height: 2px;
    background-color: #d9859f;
}

.gpt3__navbar-links_container a:hover:before {
    width: 100%;
}


.gpt3__navbar-links_container a {
    color: #fff;
}

.gpt3__navbar-menu_container a {
    color: #fff;
}

.gpt3__navbar-sign .colored,
.gpt3__navbar-menu_container .colored {
    padding: 0.5rem 1rem;
    color: #fff;
    background: #FF4820;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    border: none;
    transition: 0.3s;
    outline: none;
    cursor: pointer;
    border-radius: 5px;
}

.gpt3__navbar-sign .colored:hover {
    background-color: #e7340b;
}

.gpt3__navbar-menu {
    margin-left: 1rem;
    display: none;
    position: relative;
}

.gpt3__navbar-menu svg {
    cursor: pointer;
}

.gpt3__navbar-menu_container {
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;
    text-align: end;
    background: var(--color-footer);
    padding: 2rem;
    position: absolute;
    right: 0;
    top: 40px;
    margin-top: 1rem;
    min-width: 210px;
    border-radius: 5px;
    box-shadow: 0px 0px 5px rgba(0,0, 0,0.2);
}

.gpt3__navbar-menu_container a {
    margin: 1rem 0;
    display: inline-block;
    transition: 0.3s;
}

.gpt3__navbar-menu_container a:hover {
    color: #d9859f;
}

.gpt3__navbar-menu_container .colored:hover {
    color: #fff;
    background-color: #e7340b;
}

.gpt3__navbar-menu_container-links-sign {
    display: none;
}

@media screen and (max-width: 1050px) {
    .gpt3__navbar-links_container {
        display: none;
    }

    .gpt3__navbar-menu {
        display: flex;
    }
}

@media screen and (max-width: 700px) {
    .gpt3__navbar {
        padding: 2rem 4rem;
    }
}

@media screen and (max-width: 550px) {
    .gpt3__navbar {
        padding: 2rem;
    }

    .gpt3__navbar-sign {
        display: none;
    }

    .gpt3__navbar-menu_container {
        top: 20px;
    }

    .gpt3__navbar-menu_container-links-sign {
        display: block;
    }
}