.gpt3__subscribe__content {
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: center;
}
.gpt3__subscribe-container {
    display: flex;
    margin-top: 2rem
}
.gpt3__subscribe__heading h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 40px;
}

.gpt3__subscribe-content__input {
    display: flex;
    width: 100%;
    margin: 2rem 0 1rem;
}

.gpt3__subscribe-content__input input {
    line-height: 27px;
    font-size: 20px;
    padding: 0 1rem;
    background: #0a1f3e;
    font-family: var(--font-family);
    border: 1px solid var(--footer-color);
    color: #fff;
    margin-left: 3rem;
    outline: none;
    border-radius: 5px 0px 0px 5px;
}

.gpt3__subscribe-content__input a {
    padding: 0.5rem 1rem;
    color: #fff;
    background: #FF4820;
    font-family: var(--font-family);
    font-weight: bold;
    font-size: 18px;
    line-height: 25px;
    border: none;
    outline: none;
    cursor: pointer;
    transition: 0.3s;
    border-radius: 0 5px 5px 0;
}

.gpt3__subscribe-content__input a:hover {
    background-color: #e7340b;
}

@media (max-width: 650px) {
    .gpt3__subscribe__content {
        text-align: center;
        flex-direction: column;
    }
    .gpt3__subscribe-content__input input {
        margin-left: 0;
    }
    .gpt3__subscribe-content__input input ,
    .gpt3__subscribe-content__input a {
        font-size: 16px;
    }
}