.gpt3__header {
    display: flex;
    /* padding: 0 6rem 4rem 6rem;  */
}

.gpt3__header-content {
    display: flex;
    align-items: flex-start;
    justify-content: center;
    flex-direction: column;
}

.gpt3__header-content h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 45px;
    letter-spacing: -0.04em;
    margin-bottom: 2rem;
}

.gpt3__header-content p {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 20px;
    color: #81AFDD;
}

.gpt3__header-content__input {
    display: flex;
    width: 100%;
    margin: 2rem 0 1rem;
}

.gpt3__header-content__input input {
    line-height: 27px;
    font-size: 20px;
    padding: 0 1rem;
    background: #0a1f3e;
    font-family: var(--font-family);
    border: 1px solid var(--footer-color);
    color: #fff;
    outline: none;
    border-radius: 5px 0px 0px 5px;
}

.gpt3__header-content__input a {
    padding: 0.5rem 1rem;
    color: #fff;
    background: #FF4820;
    font-family: var(--font-family);
    font-weight: bold;
    font-size: 18px;
    line-height: 25px;
    border: none;
    outline: none;
    cursor: pointer;
    transition: 0.3s;
    border-radius: 0 5px 5px 0;
}

.gpt3__header-content__input a:hover {
    background-color: #e7340b;
}

.gpt3__header-content__people {
    display: flex;
    align-items: flex-start;
    justify-content: center;
    margin-top: 2rem;
}

.gpt3__header-content__people img {
    width: 181.79px;
    height: 38px;
}

.gpt3__header-content__people p {
    font-family: var(--font-family);
    margin-left: 0.5rem;
    color: #fff;
    font-size: 12px;
    font-weight: 500;
    text-align: center;
}

.gpt3__header-content-image {
    display: flex;
    align-items: center;
    justify-content: center;
}

.gpt3__header-content-image img {
    width: 100%;
    height: 100%;
}

@media (max-width: 1050px) {
    .gpt3__header {
        flex-direction: column;
    }
    .gpt3__header-content {
        margin: 0 0 3rem;
    }
}

@media (max-width: 650px) {
    .gpt3__header-content h1 {
        font-size: 40px;
        line-height: 60px;
        margin-bottom: 1.5rem;
    }
    .gpt3__header-content p {
        font-size: 16px;
        line-height: 24px;
    }
    .gpt3__header-content__people {
        flex-direction: column;
        align-items: center;
    }
    .gpt3__header-content__people p {
        text-align: center;
        margin-top: 1rem;
    }
    .gpt3__header-content__input a {
        text-align: center;
    }
    .gpt3__header-content__input input ,
    .gpt3__header-content__input a {
        font-size: 16px;
    }
}