.gpt3__cta {
    border: 1px solid transparent;
    margin: 6rem;
    padding: 2rem;
    color: #000;
    display: flex;
    justify-content: space-between;
    border-radius: 5px;
    background: var(--gradient-bar);
    align-items: center;
}

.gpt3__cta-content p {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 12px;
    line-height: 30px;
    color: #0E0E0E;
}

.gpt3__cta-content h3 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 24px;
    line-height: 45px;
}

.gpt3__cta-link a {
    font-family: var(--font-family);
    color: #fff;
    background-color: #000;
    padding: 8px 15px;
    border-radius: 15px;
    display: inline-block;
}

@media screen and (max-width: 650px) {
    .gpt3__cta {
        flex-direction: column;
    }
    .gpt3__cta-link a {
        margin: 2rem 0 0;
    }
}

@media screen and (max-width: 550px) {
    .gpt3__cta {
        margin: 4rem 2rem;
    }
    .gpt3__cta-content h3 {
        font-size: 18px;
        line-height: 32px;
    }
    .gpt3__cta-link a {
        font-size: 14px;
        margin-top: 2rem;
        line-height: 28px;
    }
}